  background-repeat: no-repeat;
  background-position: center center;
}
body {
  background-color: #F5F5F5;
}
.line-bottom {
  border-bottom: 1px solid #d4d4d4;
}
.line-top {
  border-top: 1px solid #d4d4d4;
}
.single-row {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.author-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.button {
  padding: 2px 5px;
  border-radius: 5px;
  background-color: #42b983;
}
ul {
  list-style: none;
}
body {
  font-size: 0;
